/* META ROOT */
:root {
  /* Enhanced Background Gradients */
  --bg-gradient-onyx: linear-gradient(
    to bottom right,
    hsl(240, 3%, 30%) 10%,
    hsl(240, 2%, 20%) 90%
  );
  --bg-gradient-jet: linear-gradient(
      to bottom right,
      hsla(240, 5%, 25%, 0.3) 10%,
      hsla(240, 3%, 15%, 0) 100%
    ),
    hsl(240, 2%, 18%);
  --bg-gradient-yellow1: linear-gradient(
    to bottom right,
    hsl(45, 95%, 65%) 0%,
    hsla(45, 85%, 55%, 0.1) 60%
  );
  --bg-gradient-yellow2: linear-gradient(
      135deg,
      hsla(45, 90%, 60%, 0.3) 0%,
      hsla(45, 80%, 50%, 0) 60%
    ),
    hsl(240, 3%, 18%);
  --border-gradient-onyx: linear-gradient(
    to bottom right,
    hsl(0, 0%, 28%) 0%,
    hsla(0, 0%, 25%, 0) 50%
  );

  /* Enhanced Font Gradients */
  --text-gradient-yellow: linear-gradient(
    to right,
    hsl(45, 100%, 75%),
    hsl(35, 90%, 70%)
  );

  /* Refined Colors */
  --jet: hsl(0, 0%, 25%);
  --onyx: hsl(240, 3%, 20%);
  --eerie-black1: hsl(240, 3%, 18%);
  --eerie-black2: hsl(240, 2%, 15%);
  --smoky-black: hsl(0, 0%, 10%);
  --white1: hsl(0, 0%, 100%);
  --white2: hsl(0, 0%, 95%);
  --orange-yellow-crayola: hsl(45, 95%, 65%);
  --vegas-gold: hsl(45, 65%, 55%);
  --light-gray: hsl(0, 0%, 88%);
  --light-gray70: hsla(0, 0%, 88%, 0.7);
  --bittersweet-shimmer: hsl(0, 50%, 53%);

  /* Typography */
  /* --ff-poppins: "Poppins", sans-serif; */
  /* --ff-primary: "Inter", sans-serif; */
  /* --ff-heading: "Playfair Display", serif; Heading font (Playfair Display) */

  --ff-primary: "Roboto", sans-serif; /* Body font (Roboto) */
  --ff-heading: "Merriweather", serif; /* Heading font (Merriweather) */
  /* Font Sizes */
  --fs1: 28px; /* Headings */
  --fs2: 22px; /* Subheadings */
  --fs3: 18px; /* Content */
  --fs4: 16px; /* Smaller Content */
  --fs5: 15px;
  --fs6: 14px;
  --fs7: 13px;
  --fs8: 12px;

  /* Font Weights */
  --fw300: 300;
  --fw400: 400;
  --fw500: 500;
  --fw600: 600;

  /* Shadows */
  --shadow1: -4px 8px 24px hsla(0, 0%, 0%, 0.2);
  --shadow2: 0px 16px 30px hsla(0, 0%, 0%, 0.25);
  --shadow3: 0px 16px 40px hsla(0, 0%, 0%, 0.3);
  --shadow4: 0px 25px 50px hsla(0, 0%, 0%, 0.2);
  --shadow5: 0px 24px 80px hsla(0, 0%, 0%, 0.3);

  /* Transitions */
  --transition1: 0.3s ease;
  --transition2: 0.6s ease-in-out;
}


*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

img,
ion-icon,
a,
button,
time,
span {
  display: block;
}

button {
  font: inherit;
  background: none;
  border: none;
  text-align: left;
  cursor: pointer;
}

input,
textarea {
  display: block;
  width: 100%;
  background: none;
  font: inherit;
}

::selection {
  background: var(--orange-yellow-crayola);
  color: var(--smoky-black);
}

:focus {
  outline-color: var(--orange-yellow-crayola);
}

html {
  font-family: var(--ff-primary);
}

body {
  background: var(--smoky-black);
}

main {
  margin: 15px 12px;
  margin-bottom: 75px;
  min-width: 259px;
}

.sidebar,
article {
  background: var(--eerie-black2);
  border: 1px solid var(--jet);
  border-radius: 20px;
  box-shadow: var(--shadow1);
  z-index: 1;
  padding: 15px;
}

.sidebar.active {
  max-height: 405px;
}

.separator {
  width: 100%;
  height: 1px;
  background: var(--jet);
  margin: 16px 0;
}

.icon-box {
  position: relative;
  background: var(--border-gradient-onyx);
  width: 30px;
  height: 30px;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  color: var(--orange-yellow-crayola);
  box-shadow: var(--shadow1);
  z-index: 1;
}

.icon-box::before {
  content: "";
  position: absolute;
  inset: 1px;
  background: var(--eerie-black1);
  border-radius: inherit;
  z-index: -1;
}

.icon-box ion-icon {
  --ionicon-stroke-width: 35px;
}

article {
  display: none;
}

article.active {
  display: block;
  animation: fade 0.5s ease backwards;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.h2,
.h3,
.h4,
.h5 {
  color: var(--white2);
  text-transform: capitalize;
}

.h2 {
  font-size: var(--fs1);
}
.h3 {
  font-size: var(--fs2);
}
.h4 {
  font-size: var(--fs4);
}
.h5 {
  font-size: var(--fs7);
  font-weight: var(--fw500);
}

/* Heading 2 */
.h2 {
  font-family: var(--ff-heading); /* Use Playfair Display for h2 */
  font-size: var(--fs1); /* Heading 2 font size */
  font-weight: var(--fw600); /* Strong weight for emphasis */
}

/* Heading 3 */
.h3 {
  font-family: var(--ff-heading); /* Use Playfair Display for h3 */
  font-size: var(--fs2); /* Heading 3 font size */
  font-weight: var(--fw500); /* Slightly lighter weight */
}

/* Heading 4 */
.h4 {
  font-family: var(--ff-heading); /* Use Playfair Display for h4 */
  font-size: var(--fs4); /* Heading 4 font size */
  font-weight: var(--fw500); /* Consistent with h3 for readability */
}

/* Heading 5 */
.h5 {
  font-family: var(--ff-heading); /* Use Playfair Display for h5 */
  font-size: var(--fs7); /* Heading 5 font size */
  font-weight: var(--fw400); /* Regular weight for a softer look */
}

.article-title {
  position: relative;
  padding-bottom: 7px;
}

.article-title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 30px;
  height: 3px;
  background: var(--text-gradient-yellow);
  border-radius: 3px;
}

.has-scrollbar::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

.has-scrollbar::-webkit-scrollbar-track {
  background: var(--onyx);
  border-radius: 5px;
}

.has-scrollbar::-webkit-scrollbar-thumb {
  background: var(--orange-yellow-crayola);
  border-radius: 5px;
}

.has-scrollbar::-webkit-scrollbar-button {
  width: 20px;
}

.content-card {
  position: relative;
  background: var(--border-gradient-onyx);
  padding: 15px;
  padding-top: 45px;
  border-radius: 14px;
  box-shadow: var(--shadow2);
  cursor: pointer;
  z-index: 1;
}

.content-card::before {
  content: "";
  position: absolute;
  inset: 1px;
  background: var(--bg-gradient-jet);
  border-radius: inherit;
  z-index: -1;
}

/*ASIDE - SIDEBAR*/

.sidebar {
  margin-bottom: 15px;
  max-height: 112px;
  overflow: hidden;
  padding: 15px;
  transition: var(--transition2);
}

.sidebar-info {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 15px;
}

.avatar-box {
  background: var(--bg-gradient-onyx);
  border-radius: 20px;
}

.info-content .name {
  color: var(--white2);
  font-size: var(--fs3);
  font-weight: var(--fw500);
  letter-spacing: -0.25px;
  margin-bottom: 10px;
}

.info-content .title {
  color: var(--white1);
  background: var(--onyx);
  font-size: var(--fs8);
  font-weight: var(--fw300);
  width: max-content;
  padding: 3px 12px;
  border-radius: 8px;
}

.info-more-btn {
  position: absolute;
  top: -15px;
  right: -15px;
  border-radius: 0 15px;
  font-size: 13px;
  color: var(--orange-yellow-crayola);
  background: var(--border-gradient-onyx);
  padding: 10px;
  box-shadow: var(--shadow2);
  transition: var(--transition1);
  z-index: 1;
}

.info-more-btn::before {
  content: "";
  position: absolute;
  inset: 1px;
  border-radius: inherit;
  background: var(--bg-gradient-jet);
  transition: var(--transition1);
  z-index: -1;
}

.info-more-btn:hover,
.info-more-btn:focus {
  background: var(--bg-gradient-yellow1);
}
.info-more-btn:hover::before,
.info-more-btn:focus::before {
  background: var(--bg-gradient-yellow2);
}

.info-more-btn span {
  display: none;
}

.sidebar-info-more {
  opacity: 0;
  visibility: hidden;
  transition: var(--transition2);
}

.sidebar.active .sidebar-info-more {
  opacity: 1;
  visibility: visible;
}

.contacts-list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
}

.contact-item {
  min-width: 100%;
  display: flex;
  align-items: center;
  gap: 16px;
}

.contact-info {
  max-width: calc(100% - 46px);
  width: calc(100% - 46px);
}

.contact-info :is(.contact-link, time, address) {
  color: var(--white2);
  font-size: var(--fs7);
}

.contact-info address {
  font-style: normal;
}

.contact-title {
  color: var(--light-gray70);
  font-size: var(--fs8);
  text-transform: uppercase;
  margin-bottom: 2px;
}

.social-list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 15px;
  padding-bottom: 4px;
  padding-left: 7px;
}

.social-item .social-link {
  color: var(--light-gray70);
  font-size: 18px;
}

.social-item .social-link:hover {
  color: var(--light-gray);
}

/*NAVBAR*/

.navbar {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: hsla(240, 1%, 17%, 0.75);
  backdrop-filter: blur(10px);
  border: 1px solid var(--jet);
  border-radius: 12px 12px 0 0;
  box-shadow: var(--shadow2);
  z-index: 5;
}

.navbar-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
}

.navbar-link {
  color: var(--light-gray);
  font-size: var(--fs8);
  padding: 20px 7px;
  transition: color var(--transition1);
}

.navbar-link:hover,
.navbar-link:focus {
  color: var(--light-gray70);
}
.navbar-link.active {
  color: var(--orange-yellow-crayola);
}

/*ABOUT*/

.about .article-title {
  margin-bottom: 15px;
  margin-top: 15px;
}

.project .article-title {
  margin-bottom: 15px;
  margin-top: 15px;
}

.about-text {
  color: var(--light-gray);
  font-size: var(--fs6);
  font-weight: var(--fw300);
  line-height: 1.6;
}

.about-text p {
  margin-bottom: 15px;
  text-align: justify;
  padding-left: 10px;
  padding-right: 10px;
}

.service {
  margin-bottom: 35px;
}

.service-title {
  margin-bottom: 20px;
}

.service-list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}

.service-item {
  position: relative;
  background: var(--border-gradient-onyx);
  padding: 20px;
  border-radius: 14px;
  box-shadow: var(--shadow2);
  z-index: 1;
}

.service-item::before {
  content: "";
  position: absolute;
  inset: 1px;
  background: var(--bg-gradient-jet);
  border-radius: inherit;
  z-index: -1;
}

.service-icon-box {
  margin-bottom: 10px;
}
.service-icon-box img {
  margin: auto;
}

.service-content-box {
  text-align: center;
}
.service-item-title {
  margin-bottom: 7px;
}

.service-item-text {
  color: var(--light-gray);
  font-size: var(--fs6);
  font-weight: var(--fw300);
  line-height: 1.6;
}

/*TESTIMONIALS*/

.testimonials {
  margin-bottom: 30px;
}
.testimonials-title {
  margin-bottom: 20px;
}

.testimonials-list {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 15px;
  margin: 0 -15px;
  padding: 25px 15px;
  padding-bottom: 35px;
  overflow-x: auto;
  scroll-behavior: smooth;
  overscroll-behavior-inline: contain;
  scroll-snap-type: inline mandatory;
}

.testimonials-item {
  min-width: 100%;
  scroll-snap-align: center;
}

.testimonials-avatar-box {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(15px, -25px);
  background: var(--bg-gradient-onyx);
  border-radius: 14px;
  box-shadow: var(--shadow1);
}

.testimonials-text {
  color: var(--light-gray);
  font-size: var(--fs6);
  font-weight: var(--fw300);
  line-height: 1.6;
  display: -webkit-box;
  line-clamp: 4;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

/*MODAL CONTAINER*/

.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
  overscroll-behavior: contain;
  z-index: 20;
  pointer-events: none;
  visibility: hidden;
}

.modal-container::-webkit-scrollbar {
  display: none;
}

.modal-container.active {
  pointer-events: all;
  visibility: visible;
}

.modal-container.active .testimonials-modal {
  transform: scale(1);
  opacity: 1;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: hsl(0, 0%, 5%);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 1;
  transition: var(--transition1);
}

.overlay.active {
  opacity: 0.8;
  visibility: visible;
  pointer-events: all;
}

.testimonials-modal {
  background: var(--eerie-black2);
  position: relative;
  padding: 15px;
  margin: 15px 12px;
  border: 1px solid var(--jet);
  border-radius: 14px;
  box-shadow: var(--shadow5);
  transform: scale(1.2);
  opacity: 0;
  transition: var(--transition1);
  z-index: 2;
}

.modal-close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  background: var(--onyx);
  border-radius: 8px;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--white2);
  font-size: 18px;
  opacity: 0.7;
}

.modal-close-btn:hover,
.modal-close-btn:focus {
  opacity: 1;
}
.modal-close-btn ion-icon {
  --ionicon-stroke-width: 50px;
}

.modal-avatar-box {
  background: var(--bg-gradient-onyx);
  width: max-content;
  border-radius: 14px;
  margin-bottom: 15px;
  box-shadow: var(--shadow2);
}

.modal-img-wrapper > img {
  display: none;
}

.modal-title {
  margin-bottom: 4px;
}

.modal-content time {
  font-size: var(--fs6);
  color: var(--light-gray70);
  font-weight: var(--fw500);
  margin-bottom: 10px;
}

.modal-content p {
  color: var(--light-gray);
  font-size: var(--fs6);
  font-weight: var(--fw300);
  line-height: 1.6;
}

/*CLIENTS*/

.clients {
  margin-bottom: 15px;
}

.clients-list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 15px;
  margin: 0 -15px;
  padding: 25px;
  padding-bottom: 25px;
  overflow-x: auto;
  scroll-behavior: smooth;
  overscroll-behavior-inline: contain;
  scroll-snap-type: inline mandatory;
  scroll-padding-inline: 25px;
}

.clients-item {
  min-width: 50%;
  scroll-snap-align: start;
}

.clients-item img {
  width: 100%;
  /* filter: grayscale(1); */
  transition: var(--transition1);
}

/* .clients-item img:hover {
  filter: grayscale(0);
} */

/*RESUME*/

.article-title {
  margin-bottom: 30px;
}

.timeline {
  margin-bottom: 30px;
}

.timeline .title-wrapper {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 25px;
}

.timeline-list {
  font-size: var(--fs6);
  margin-left: 45px;
}

.timeline-list span {
  color: var(--vegas-gold);
  font-weight: var(--fw400);
  line-height: 1.6;
}

.timeline-item {
  position: relative;
}

.timeline-item:not(:last-child) {
  margin-bottom: 20px;
}
.timeline-item:not(:last-child)::before {
  content: "";
  position: absolute;
  top: -25px;
  left: -30px;
  width: 1px;
  height: calc(100% + 50px);
  background: var(--jet);
}

.timeline-item::after {
  content: "";
  position: absolute;
  top: 5px;
  left: -33px;
  height: 6px;
  width: 6px;
  border-radius: 50%;
  background: var(--text-gradient-yellow);
  box-shadow: 0 0 0 4px var(--jet);
}

.timeline-item-title {
  font-size: var(--fs6);
  line-height: 1.3;
  margin-bottom: 7px;
}

.timeline-text {
  color: var(--light-gray);
  font-weight: var(--fw300);
  line-height: 1.6;
  text-align: justify;
}

/*SKILLS*/
.skills-title {
  margin-bottom: 20px;
  text-align: center;
}

.skills-category {
  margin-bottom: 30px;
}

.skills-category-title {
  font-size: var(--fs2);
  text-align: center;
  color: var(--white2);
  text-transform: capitalize;
  margin-bottom: 10px;
}

.skills-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 15px;
  list-style: none;
  padding: 0;
  justify-items: center;
  text-align: center;
}

.skills-item {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: var(--border-gradient-onyx);
  color: var(--orange-yellow-crayola);
  padding: 13px 10px; /* Adjusted padding to match .form-btn */
  width: 140px;
  height: 56px;
  border-radius: 14px; /* Adjusted border-radius to match .form-btn */
  font-size: var(--fs5); /* Adjusted font size to match .form-btn */
  margin: 0 auto; /* Ensure items are centered */
  position: relative;
  z-index: 1;
  transition: var(--transition1);
}

.skills-item::before {
  content: "";
  position: absolute;
  inset: 1px;
  background: var(--bg-gradient-jet); /* Default background gradient */
  border-radius: inherit;
  z-index: -1;
  transition: var(--transition1);
}

.skills-item:hover {
  background: var(--bg-gradient-yellow1); /* Hover effect for background */
}

.skills-item::before {
  background: var(--bg-gradient-yellow2); /* Hover effect for ::before */
}

.skills-item ion-icon {
  font-size: 16px; /* Match icon size to .form-btn */
}


/* Ensure grid items in the last column are also aligned */
.skills-list > .skills-item:nth-child(4n) {
  justify-self: center;
}
.skills-list > .skills-item:nth-child(3n) {
  justify-self: center;
}

/*PORTFOLIO*/

.filter-list {
  display: none;
}

.filter-select-box {
  position: relative;
  margin-bottom: 25px;
}

.filter-select {
  background: var(--eerie-black2);
  color: var(--light-gray);
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 12px 16px;
  border: 1px solid var(--jet);
  border-radius: 14px;
  font-size: var(--fs6);
  font-weight: var(--fw300);
}

.filter-select.active .select-icon {
  transform: rotate(0.5turn);
}

.select-list {
  background: var(--eerie-black2);
  position: absolute;
  top: calc(100% + 6px);
  width: 100%;
  padding: 6px;
  border: 1px solid var(--jet);
  border-radius: 14px;
  z-index: 2;
  visibility: hidden;
  pointer-events: none;
  transition: 0.5s ease-in-out;
}

.filter-select.active + .select-list {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.select-item button {
  background: var(--eerie-black2);
  color: var(--light-gray);
  font-size: var(--fs6);
  font-weight: var(--fw300);
  text-transform: capitalize;
  width: 100%;
  padding: 8px 10px;
  border-radius: 8px;
}

.select-item button:hover {
  --eerie-black2: hsl(240, 2%, 20%);
}

.project-list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
  margin-bottom: 10px;
  
}



.project-item {
  display: none;
  /* border-bottom-left-radius:16px; */
  /* border-bottom-right-radius:16px; ; */
  border-radius: 14px;
 
}

.project-item.active {
  display: block;
  animation: scaleUp 0.25s ease forwards;
  /* this is background*/
  /* background: hsla(0, 0%, 0%, 0.5); */
  background: var(--bg-gradient-yellow2);

}

@keyframes scaleUp {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}

.project-item > a {
  width: 100%;
}

.project-img {
  position: relative;
  width: 100%;
  height: 200px;
  border-radius: 16px;
  overflow: hidden;
  margin-bottom: 15px;
}

.project-img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: var(--transition1);
}

.project-img::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  z-index: 1;
  transition: var(--transition1);

}


.project-item > a:hover .project-img::before {
  background: hsla(0, 0%, 0%, 0.5);
}

.project-item-icon-box {
  --scale: 0.8;

  background: var(--jet);
  color: var(--orange-yellow-crayola);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(var(--scale));
  font-size: 20px;
  padding: 18px;
  border-radius: 12px;
  opacity: 0;
  z-index: 1;
  transition: var(--transition1);
}

.project-item > a:hover .project-item-icon-box {
  --scale: 1;
  opacity: 1;
}

.project-item-icon-box ion-icon {
  --ionicon-stroke-width: 50px;
}

.project-title,
.project-category {
  text-align: center;
  font-size:var(--fs5);
  margin-bottom: 12px;  
  color: var(--orange-yellow-crayola);
}

.project-title {
  /* color: var(--white2); */
  font-size: var(--fs5);
  font-weight: var(--fw400);
  text-transform: capitalize;
  line-height: 1.3;

}

.project-category {
  color: var(--light-gray70);
  font-size: var(--fs6);
  font-weight: var(--fw300);

}

/*BLOG*/

.blog-posts {
  margin-bottom: 10px;
}

.blog-posts-list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}

.blog-posts-item > a {
