  font-size: var(--fs6);
  font-weight: var(--fw400);
  padding: 13px 20px;
  border: 1px solid var(--jet);
  border-radius: 14px;
  outline: none;
}


.form-input,.select-form-input {
  color: var(--white2);
  font-size: var(--fs6);
  font-weight: var(--fw400);
  padding: 13px 20px;
  border: 1px solid var(--jet);
  border-radius: 14px;
  outline: none;
}

.form-input::placeholder {
  font-weight: var(--fw500);
}
.form-input:focus {
  border-color: var(--orange-yellow-crayola);
}
.form-input:focus:invalid {
  border-color: var(--bittersweet-shimmer);
}

textarea.form-input {
  min-height: 100px;
  height: 120px;
  max-height: 200px;
  resize: vertical;
  margin-bottom: 25px;
}

textarea.form-input::-webkit-realizer {
